/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~ Body ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */

body {
  background-color: rgba(0, 0, 0, 0.1);
  min-width: 300vw;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

body::-webkit-scrollbar {
  display: none;
}

.blank_one {
  flex: 1;
}

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~ Body ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */


/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~ Container ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */
.container {
  position: fixed;
  margin: 0;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-flow: column;
}

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~~~ Container ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~ */

#header {
  flex: 1;
  background-color: rgba(0, 0, 255, 0);
  display: flex;
}

#title {
  flex: 1;
  background-color: rgba(0, 255, 255, 0);
  color: #ffffff;
  font-size: 42px;
}

#title_name {
  background-color: rgba(0, 255, 255, 0);
  margin: 40px 0px 0px 60px;
}

#title_portfolio {
  background-color: rgba(0, 255, 255, 0);
  margin: 0px 0px 0px 100px;
}

#title_blank {
  flex: 2;
}

#menu {
  flex: 1;
  background-color: rgba(0, 255, 255, 0);
  display: flex;
}

#menu_blank_first {
  flex: 1;
}

#list_view {
  flex: 2;
  background-color: rgba(0, 255, 255, 0);
  font-size: 20px;
}

#list_view li {
  list-style: none;
  margin: 0;
}

#list_view div {
  display: inline-block;
  position: relative;
  overflow: hidden;
  color: #ffffff;
  text-decoration: none;
  cursor: pointer;
}

#list_view div:after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #fff;
  transform: translate(-100%, 0);
  transition: transform cubic-bezier(0.215, 0.61, 0.455, 1) 0.4s;
  content: "";
}

#list_view div:hover:after {
  transform: translate(0, 0);
}

#menu_blank_second {
  flex: 1;
}

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~ Content ~~~~~~~~~~~~~~~~~~~~~~~ */

#content {
  flex: 4;
  background-color: rgba(255, 0, 0, 0);
  display: flex;
}

/* ~~~~~~~~~~~~~~~~~~~~~~~~~~ Hello ~~~~~~~~~~~~~~~~~~~~~~~~~ */

#content_hello {
  flex: 1;
  display: flex;
  flex-flow: column;
  text-align: center;
}

#content_hello_content {
  height: inherit;
  flex: 1;
  background-color: rgba(255, 0, 0, 0);
  display: flex;
}

#content_hello_content_content {
  flex: 2;
  background-color: rgba(255, 0, 0, 0);
}

#content_hello_welcome {
  font-size: 104px;
